.accomodation-container {
    max-width: 95%;
    margin: auto;
    background-color: #F6F6F6;
    border-radius: 25px;
    display: flex;
    flex-wrap: wrap;
    margin-top: 3em;
    padding-top: 3em;
    margin-bottom: 2em;
}

.accomodation-container > a {
    text-decoration: none;
    margin: auto;
}

.accomodation-container > a:hover {
    text-decoration: none;
    opacity: 0.9;
}

.thumb {
    height: 18em;
    width: 18em;
    margin: auto; 
}

.thumb img {
    width: 90%;
    height: 90%;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
    border-radius: 10px;
}

.thumb h2 {
    margin-top: -3.7em;
    font-size: 0.9em;
    font-weight: 500;
    color: white;
    overflow-wrap: break-word;
    width: 12.5em;
    margin-left: 0.5em;
    text-shadow: 1px 1px 3px rgba(0,0,0,0.5);
    opacity: 0.9;
}


@media only screen and (max-width: 426px) {
    .accomodation-container > a {
        padding-left: 0em;
        width: 100%;
        height: 200px;
        margin-bottom: 5em;
    }

   .accomodation-container{
        margin: 0em;
        padding: 0em;
        padding-top: 1.4em;
        background-color: white;
        display: grid;
        grid-template-columns: repeat(1);
        margin: auto;
    }

    .thumb {
        width: 100%;
        padding-bottom: 1em;
        height: 300px;
    }

 .thumb img {
        width: 100%;
    } 
    
}    

