.error404 {
    text-align: center;
}

.error404 h2 {
   font-weight: 700;
   font-size: 10em;
   margin-bottom: 0em;
}

.error404 h3 {
    font-weight: 500;
    font-size: 2em;
    margin-bottom: 3em;
 }

 .errorpage > a {
     text-decoration: underline;
     display: flex;
     justify-content: center;
 }

 .errorpage > a:hover {
    color: black;
}

 .errorpage {
    margin-top: 8em;
    height: 500px;
    margin-bottom: 6em;
 }


 @media screen and (max-width: 425px){

    .error404 h2 {
       font-weight: 700;
       font-size: 3.4em;
       margin-bottom: 0em;
    }
    
    .error404 h3 {
        font-weight: 500;
        font-size: 1em;
        margin-bottom: 3em;
        padding: 3.4em;
        padding-top: 1em;
     }

     .errorpage > a {
        padding-left: 0em;
        font-size: 0.8em;
     }

     .errorpage {
        margin-bottom: -3.6em;
        margin-top: 5em;
     }

  }