
.gallery-img {
width: 100%;
object-fit: contain;
vertical-align: top;
} 

.gallery-container {
    position: relative;
    justify-content: center;
    height: 20em;
    width: 100%;
    margin: auto;
    text-align: center;
    border-radius: 25px;
    overflow: hidden;
    margin-bottom: 2em; 
}

.arrow-right {
    position: absolute;
    top: 40%;
    right: 3em;
    z-index: 100;
    cursor: pointer;
    width: 2.4em;
}

.arrow-left {
    position: absolute;
    top: 40%;
    left: 3em;
    z-index: 100;
    cursor: pointer;
    width: 2.4em;
}

.index {
    color: white;
    position: absolute;
    top: 88%;
    left: 48%;
    z-index: 1;
    display: flex;
    justify-content: center;
    font-size: 1.4em;
}

@media screen and (min-width: 1100px){
    .gallery-container {
        height: 22em; 
    }
     
   }


 @media screen and (min-width: 1600px){
    .gallery-container {
        height: 34em; 
    }
     
   }

   @media screen and (max-width: 426px){

    .gallery-container {
        margin-bottom: 1em;
    }

    .wrapper {
        margin-top: 1.4em;
    }
    .gallery-container {
        height: 12em; 
    }

    .arrow-right {
        width: 0.9em;
        top: 45%;
        right: 0.4em;
    }
    
    .arrow-left {
        width: 0.9em;
        top: 45%;
        left: 0.4em;
    }

    .index {
        top: 88%;
        font-size: 1em;
    }
     
   }